    margin: 0;
}

nav {
    display: flex;
    justify-content: center;
    align-items: center;
}

button {
    width: 245px;
    height: 64px;
    border: 1px solid white;
    background-color: black;
    color:white;
    text-transform: uppercase;
    font-family: 'League Spartan';
    font-size:17px;
    font-weight: 600;
    letter-spacing: 2.5px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
}

.midSection {
    background-color: #111111;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 20px;
}

.midSection article{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    /* gap: 100px; */
}

.midSection > article > div {
    color: white;
}

.midSection .cardsContainer {
    display: flex;
    flex-direction: column;
    gap: 120px;
}

.midSection.card {
    display: flex;
    flex-direction: column;
    gap: 50px;
    color: white;
}

.midSection .card > div{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.midsection .card div h4{
    font-family: 'League Spartan';
    font-size:20px;
    font-weight: 600;
    margin-top: 0;
}

.midSection .card div p{
    font-family: 'League Spartan';
    font-size:16px;
    line-height: 26px;
}

.midSection h3 {
    font-family: 'League Spartan';
    color:white;
    font-size:48px;
    font-weight: 600;
    line-height: 48px;
}

.midSection h4 {
    font-family: 'League Spartan';
    color:white;
    font-weight: 600;
    line-height: 48px;
}

.midSection p{
    font-family: 'League Spartan';
    color:white;
    font-size:20px;
    line-height: 30px;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
    display: none;
    position: absolute;
    background-color: #1b313b;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
    border-radius: 10px;
  }
  
  /* Links inside the dropdown */
  .dropdown-content a {
    color: white;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
  

  /* Change color of dropdown links on hover */
  .dropdown-content a:hover {
    color: black;
    background-color: #e9c46a;
    border-radius: inherit;
  }
  
  .dropdown-content a:active {
    color: black;
    background-color: #cfae5e;
    border-radius: inherit;
  }
  
  /* The container <div> - needed to position the dropdown content */
  .dropdown {
    position: relative;
    display: inline-block;
  }
  
  /* Show the dropdown menu (use JS to add this class to the .dropdown-content container when the user clicks on the dropdown button) */
  .show {
    display: block;
  }
